*, ::after, ::before {
    box-sizing: border-box;
}

body {
    padding: 0;
    font-family: Baskerville, sans-serif;
    background-color: black;
}

.container {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}

main {
    margin: 0 100px;
    flex-grow: 1;
}

p {
    color: white;
}

.navigation-link {
    color: white;
    font-size: 20px;
    text-decoration: none;
    &:hover {
        color: #535977;
    }
}
